* {
  border: none;
  margin: 0;
  padding: 0;
}

*, :before, :after {
  box-sizing: border-box;
}

a, a:link, a:visited, a:hover {
  text-decoration: none;
}

:root {
  --text-primary: #000;
  --text-secondary: #333;
  --bg-primary: #fff;
}

html {
  scroll-behavior: smooth;
}

body {
  background-image: url("bg-top.f6402442.svg");
  background-repeat: no-repeat;
  background-position: left top right;
  background-size: contain;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 14px;
}

main {
  display: block;
}

.content-wrapper {
  max-width: 1024px;
  margin: 0 auto;
  padding: 0 20px;
}

@media (max-width: 800px) {
  .content-wrapper {
    padding: 0 15px;
  }
}

.mb-15 {
  margin-bottom: 15px;
}

.mb-30 {
  margin-bottom: 30px;
}

.centered-text {
  text-align: center;
}

.header {
  height: 80px;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header__logo {
  height: 50px;
  position: relative;
}

.header__logo__container {
  background: #fff6;
  border-radius: 6px;
  padding: 5px;
}

.navigation ul, .navigation ul li {
  list-style: none;
}

.navigation ul {
  display: flex;
}

.navigation ul li {
  margin-right: 20px;
}

.navigation ul li:last-child {
  margin-right: 0;
}

.navigation ul li a {
  color: var(--text-primary);
  background: #fff6;
  border-radius: 6px;
  padding: 5px;
  font-size: 18px;
}

.navigation ul li a:hover {
  opacity: .7;
}

@media (max-width: 800px) {
  .header__logo {
    height: 40px;
  }

  .navigation ul li {
    margin-right: 10px;
  }

  .navigation ul li a {
    font-size: 16px;
  }
}

.into-section {
  height: calc(100vh - 60px);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.into-section__title {
  font-size: 40px;
}

.info-section__subtitle {
  font-size: 18px;
}

.group-section {
  padding: 40px 0;
}

.group-section__title {
  margin-bottom: 20px;
  font-size: 30px;
}

.group-section__description {
  margin-bottom: 20px;
  font-size: 16px;
}

.table-wrapper {
  max-width: 1024px;
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
  overflow-x: scroll;
}

@media (max-width: 800px) {
  .table-wrapper {
    padding: 0 15px;
  }
}

.table {
  width: 100%;
  border-collapse: collapse;
  border: none;
  margin-bottom: 20px;
}

.table thead th {
  text-align: left;
  background: #d8d8d8;
  border: none;
  border-left: 1px solid #ddd;
  border-right: 1px solid #ddd;
  padding: 10px 15px;
  font-size: 14px;
  font-weight: bold;
}

.table tbody td {
  text-align: left;
  vertical-align: middle;
  border: 1px solid #ddd;
  padding: 10px 15px;
  font-size: 14px;
}

.green-button {
  color: #fff;
  background: #70bf8a;
  border-radius: 6px;
  padding: 12px;
  font-size: 16px;
}

.footer {
  min-height: 200px;
  background-image: url("footer-bg-2.e64a82dd.svg");
  background-position: 100% 100%;
  background-repeat: no-repeat;
  background-size: contain;
  padding: 40px 15px;
}

.footer__logo {
  height: 100px;
}

.footer__content {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.footer__content__text > * {
  margin-bottom: 10px;
}

.footer__content__text > :last-child {
  margin-bottom: 0;
}

@media (max-width: 800px) {
  .footer__content {
    text-align: center;
    flex-direction: column;
  }

  .footer__content__text > * {
    margin-bottom: 15px;
  }

  .footer__logo {
    margin-bottom: 40px;
  }
}

#group-a {
  background-image: url("group-a-bottom-bg.f69cbcce.svg");
